
@media (min-width: 650px) {

    .shopCategorieContainer {
        display: flex;
        width: 100%;
        height: 620px;
        cursor: pointer;
       
    }
    
    .shopWoman {
        display: flex;
        align-items: flex-end;
        color: #fff;
        width: 50%;
        height: 100%;
        background-color: rgb(184, 184, 184);
        background-image: url(../../imgs/ShopWoman.jpg);
        background-size: cover;
        background-position: center bottom;
        background-repeat: no-repeat;
    }
    
    .shopMan {
        display: flex;
        align-items: flex-end;
        color: #fff;
        width: 50%;
        height: 100%;
        background-color: rgb(184, 184, 184);
        background-image: url(../../imgs/ShopMan.jpg);
        background-size: cover;
        background-position: center bottom;
        background-repeat: no-repeat;
    }
    
    
    
    
    .shopLinkContent {
        margin-bottom: 40px;
    }
    
    .titleShop {
        margin-left: 30px;
        margin-bottom: 10px;
        font-size: 70px;
    }
    
    
    
    .shopNow {
        margin-left: 30px;
        transition: 0.4s;
        border: 1px solid #fff;
        width: 100px;
        text-align: center;
        border-radius: 10px;
    }
    
    .shopMan:hover .shopNow,
    .shopWoman:hover .shopNow {
        margin-left: 48px;
    }

}


