

@media (min-width:650px) {
    .prodsContainer{
        width: 100%;
        margin: auto;
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        
        
    }
    
    .largerProd{
       width: 96%;
       min-width: none;
       height: 600px;
       background-image: url('../../imgs/larger.jpg');
       margin-top: 18px;
       background-size: cover;
       border: 1px solid rgb(184, 184, 184);
       cursor: pointer;
    }
    
    .largerProd h2{
        margin-top: 10px;
        margin-left: 20px;
        font-size: 2em;
        color: #ffffff;
    }
    
    .largerProd p{
        margin-top: 10px;
        margin-left: 20px;
        font-size: 1.2em;
        color: #ffffff;
    }
    
    .prod{
        min-width: 580px;
        width: auto;
        height: 640px;
        margin: 0 9px 0 9px;
        background-color: rgb(182, 182, 182);
        background-size: cover;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        border: 1px solid rgb(184, 184, 184);
        cursor: pointer;
        
    }
    
    .prod p{
        height: 25px;
        width: 100px;
        margin-right: 30px;
        margin-top: 20px;
        color:#ffffff;
        font-size: 1.2em;
        font-weight: 600;
        text-decoration: none;
    }
    
    .prod:hover p, .largerProd:hover p{
        text-decoration: underline;
    }
    
    .prod h2{
        margin-top: 10px;
        margin-left: 20px;
        font-size: 2em;
        color: #ffffff;
    
    
    }
    
    #bag{
        background-image: url('../../imgs/bag.jpg');
    }
    
    #shoes{
        background-image: url('../../imgs/shoe.jpg');
        background-position: center center;
    }
}


